import React from 'react';
import logo from './assets/logo_transparent.png';
import './App.css';
import dotenv from "dotenv";
import Publication from "./composants/Publication";
import ListPublications from "./composants/ListPublications";
import User from "./composants/User";
import Users from "./composants/Users";
import Demandes from "./composants/Demandes";
import AuthRouter from "./composants/AuthRouter";
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import Button from 'react-bootstrap/Button';

import {
  BrowserRouter as Router,
  Link,
  Route,
  Switch,
} from 'react-router-dom';

const base_url = process.env.PUBLIC_URL;

const Index = () =>  <Publication/>;
const About = () =>  <h2>About</h2>;
var users_list = () =>  <Users list_type="research"/>;
var users_list_abonnees = () =>  <Users list_type="abonnes"/>;
var users_list_abonnements = () =>  <Users list_type="abonnements"/>;
var users_list_demandes = () =>  <Demandes />;
const user_profil = () =>  <User />;
const posts_list = () =>  <ListPublications on_mur={true}/>;
const new_post = () => <Publication modifiable={true} />;
const auth = () => <AuthRouter authentified={false} signup={false} />;
const auth_signup = () => <AuthRouter authentified={false} signup={true} />;


//var textRech = "";

function setText(ev) {
	localStorage.setItem("token_search", ev.target.value)
}

function search(ev) {
	window.location = base_url + "/users/";
}

function deconnexion(ev){
	localStorage.removeItem("token");
	window.location = base_url;
}

function App() {
	dotenv.config();
  return (
    <Router>
     <div>
     <Navbar bg="dark" variant="dark">
		<a href={base_url + "/"}>
			<img src={logo}
				alt="InstaZZ" id="logo"/>
		</a>
        <Nav className="mr-auto">
          <Nav.Link href={process.env.PUBLIC_URL + "/profil/"} >Profil</Nav.Link>
          <Nav.Link href={process.env.PUBLIC_URL + "/posts/"} >Mur</Nav.Link>
          <Nav.Link href={process.env.PUBLIC_URL + "/users/demandes"} >Demandes</Nav.Link>
        </Nav>
        <Form inline>
          <FormControl type="text" placeholder="Cherchez un utilisateur" className="mr-sm-2" onChange={setText}/>
          <Button variant="outline-info" onClick={search}>Rechercher</Button>
        </Form>
		<Button variant="outline-info" id="deconnexion" onClick={deconnexion}>Deconnexion</Button>
		
      </Navbar>
       <Switch>
		 <Route path="/InstaZZ-front/" exact component={auth}/>
		 <Route path="/InstaZZ-front" exact component={auth}/>
         <Route path="/InstaZZ-front/auth" component={auth}/>
		 <Route path="/InstaZZ-front/auth/signup" component={auth_signup}/>
         <Route path="/InstaZZ-front/about/" component={About}/>
         <Route path="/InstaZZ-front/profil/:idUser" component={User}/>
         <Route path="/InstaZZ-front/profil/" component={user_profil}/>
         <Route path="/InstaZZ-front/users/demandes" component={users_list_demandes}/>
         <Route path="/InstaZZ-front/users/abonnements" component={users_list_abonnements}/>
         <Route path="/InstaZZ-front/users/abonnes" component={users_list_abonnees}/>
         <Route path="/InstaZZ-front/users/" component={users_list}/>
         <Route path="/InstaZZ-front/posts/" component={posts_list}/>
		 <Route path="/InstaZZ-front/addpost/" component={new_post}/>
       </Switch>

     </div>
   </Router>
 );
}



export default App;
